<template>
  <v-app>
    <v-navigation-drawer v-model="drawer" app clipped>
      <v-list three-line>
        <div v-if="loading">
          <v-list-item link v-for="i in 20" :key="i">
            <v-list-item-content>
              <v-list-item-title>
                <v-skeleton-loader type="text"></v-skeleton-loader>
              </v-list-item-title>
              <v-list-item-subtitle>
                <v-skeleton-loader width="140" type="text"></v-skeleton-loader>
              </v-list-item-subtitle>
              <v-list-item-subtitle>
                <v-skeleton-loader width="140" type="text"></v-skeleton-loader>
              </v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>
        </div>
        <v-list-item
          @click="view(l)"
          v-else
          v-for="(l, idx) in locations"
          :key="idx"
          link
        >
          <v-list-item-content>
            <v-list-item-title>
              <span class="font-weight-bold">
                <ICountUp
                  class="red--text text--darken-2"
                  :endVal="l.confirmed"
                />
              </span>
              {{ l["Country/Region"] }}
            </v-list-item-title>
            <v-list-item-subtitle>
              Deaths:
              <ICountUp :endVal="l.death" />
            </v-list-item-subtitle>
            <v-list-item-subtitle>
              Recovered:
              <ICountUp :endVal="l.recovered" />
            </v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar app clipped-left>
      <v-app-bar-nav-icon @click.stop="drawer = !drawer" />
      <v-spacer></v-spacer>
      <v-toolbar-title>
        C0V1D-19
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-tooltip left :dark="isDarkTheme">
        <template v-slot:activator="{ on }">
          <v-btn icon v-on="on" @click="$refs.searchDialog.dialog = true">
            <v-icon>mdi-card-search</v-icon>
          </v-btn>
        </template>
        <span>Search</span>
      </v-tooltip>
    </v-app-bar>

    <v-content>
      <v-container>
        <v-row>
          <v-col cols="12">
            <TotalCases
              :infected="cases.total_confirmed"
              :recovered="cases.total_recovered"
              :deaths="cases.total_death"
            />
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12">
            <v-card tile style="height: 70vh;" flat>
              <LeafletMap
                :data="cases.data"
                ref="map"
                @MARKER_CLICKED="viewDetails"
              />
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12">
            <p align="center">
              Created by
              <a href="https://www.github.com/roeintheglasses">Hrishi Jangir</a>
              <br />
              Data provided by
              <a href="https://github.com/CSSEGISandData/COVID-19"
                >Johns Hopkins CSSE</a
              >
            </p>
          </v-col>
        </v-row>
      </v-container>

      <!-- Details -->
      <v-dialog
        width="500"
        v-model="dialog"
        :fullscreen="$vuetify.breakpoint.xsOnly"
      >
        <v-card>
          <v-card-title>
            <span v-show="$vuetify.breakpoint.smAndUp" class="headline"
              >Confirmed Cases: {{ selected.country }}</span
            >
            <span v-show="$vuetify.breakpoint.xsOnly" class="headline">{{
              selected.country
            }}</span>
            <v-spacer></v-spacer>
            <v-btn
              v-show="$vuetify.breakpoint.xsOnly"
              @click="dialog = false"
              icon
            >
              <v-icon>mdi-close</v-icon>
            </v-btn>
          </v-card-title>
          <v-card-text>
            <div v-if="$vuetify.breakpoint.mdAndUp">
              <p>Country/Region: {{ selected["Country/Region"] }}</p>
              <p v-show="selected['Province/State']">
                Province/State: {{ selected["Province/State"] }}
              </p>

              <p>
                Confirmed:
                <ICountUp
                  class="red--text text--darken-2"
                  :endVal="selected.confirmed"
                />
              </p>
              <p>
                Deaths:
                <ICountUp :endVal="selected.death" />
              </p>
              <p>
                Recovered:
                <ICountUp :endVal="selected.recovered" />
              </p>
            </div>
            <div
              class="d-flex flex-column justify-center align-center"
              style="height: 80vh;"
              v-else
            >
              <p class="text-center">
                <span class="display-1">{{ selected["Country/Region"] }}</span>
                <br />
                <span class="title">Country Region</span>
              </p>
              <p class="text-center" v-if="selected['Province/State']">
                <span class="display-1">{{ selected["Province/State"] }}</span>
                <br />
                <span class="title">Province/State</span>
              </p>

              <p class="text-center">
                <span class="display-1">
                  <ICountUp
                    class="red--text text--darken-2"
                    :endVal="selected.confirmed"
                  />
                </span>
                <br />
                <span class="title">Confirmed</span>
              </p>

              <p class="text-center">
                <span class="display-1">
                  <ICountUp :endVal="selected.death" />
                </span>
                <br />
                <span class="title">Deaths</span>
              </p>

              <p class="text-center">
                <span class="display-1">
                  <ICountUp :endVal="selected.recovered" />
                </span>
                <br />
                <span class="title">Recovered</span>
              </p>
            </div>
          </v-card-text>
          <v-card-actions v-show="$vuetify.breakpoint.smAndUp">
            <v-spacer></v-spacer>
            <v-btn text @click="dialog = false">Close</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-content>

    <v-snackbar v-model="snackbar" bottom right :timeout="0">
      An update is available
      <v-btn dark text @click="reload">Reload</v-btn>
    </v-snackbar>

    <SearchDialog
      ref="searchDialog"
      :data="cases.data"
      @PLACE_SELECTED="viewDetails"
    />
  </v-app>
</template>

<script>
import ICountUp from "vue-countup-v2";
import { mapState, mapMutations } from "vuex";
import LeafletMap from "./components/Map.vue";
import SearchDialog from "./components/SearchDialog.vue";
import TotalCases from "./components/TotalCases.vue";
import API from "./API";

export default {
  name: "App",

  components: {
    LeafletMap,
    ICountUp,
    SearchDialog,
    TotalCases
  },

  data: () => ({
    drawer: null,
    cases: {
      total_confirmed: 0,
      total_recovered: 0,
      total_death: 0,
      data: []
    },
    selected: {},
    dialog: false,
    loading: false,
    snackbar: false
  }),

  computed: {
    ...mapState(["isDarkTheme"]),
    locations() {
      const data = [];
      this.cases.data.forEach(item => {
        const idx = data.findIndex(
          i => i["Country/Region"] === item["Country/Region"]
        );
        const { confirmed } = item.dates[item.dates.length - 1];
        const { recovered } = item.dates[item.dates.length - 1];
        const { death } = item.dates[item.dates.length - 1];
        if (idx === -1) {
          const obj = {
            ...item,
            confirmed,
            recovered,
            death
          };
          delete obj["Province/State"];
          delete obj.dates;
          data.push(obj);
        } else {
          data[idx].confirmed += confirmed;
          data[idx].recovered += recovered;
          data[idx].death += death;
        }
      });
      return data.sort((a, b) => b.confirmed - a.confirmed);
    },
    mainlandChinaCases() {
      return this.cases.data.filter(
        i => i["Country/Region"] === "Mainland China"
      );
    }
  },

  async created() {
    this.$vuetify.theme.dark = this.isDarkTheme;
    this.loading = true;
    this.cases = await API.getCases();
    this.fatalityRate = await API.getFatalityRate();
    this.loading = false;
  },

  mounted() {
    document.addEventListener("swUpdated", () => {
      this.snackbar = true;
    });
  },

  watch: {
    isDarkTheme(val) {
      this.$vuetify.theme.dark = val;
    }
  },

  methods: {
    ...mapMutations(["TOGGLE_THEME"]),
    view(location) {
      if (this.$vuetify.breakpoint.smAndDown) {
        this.drawer = false;
      }

      window.scrollTo({ top: 0, behavior: "smooth" });
      const { Lat, Long } = location;
      this.$refs.map.flyTo(Lat, Long);
    },
    viewDetails(location) {
      this.selected = {
        ...location,
        confirmed: +location.dates[location.dates.length - 1].confirmed,
        death: +location.dates[location.dates.length - 1].death,
        recovered: +location.dates[location.dates.length - 1].recovered
      };
      this.dialog = true;
    },
    endReached() {
      this.$refs.tweets.loadMore();
    },
    reload() {
      window.location.reload(true);
    }
  }
};
</script>

<style>
.ps {
  height: 70vh;
}
a {
  color: rgb(209, 236, 58);
  text-decoration: none;
}

a:hover {
  color: rgb(105, 255, 173);
}
</style>
